<template>
    <div>
        <div class="background">
            <span>手机</span>
            <div class="phone">
                <div class="dutu" v-for="item of AlargerVersion">
                    <router-link to="">
                        <img :src="item.img" alt="">
                    </router-link>
                </div>
                <div>
                </div>
                <div class="xiaolist" v-for="item of phone">
                    <span>{{item.NewProduct}}</span>
                    <img :src="item.img" alt="">
                    <p class="name">{{item.name}}</p>
                    <p class="title">{{item.title}}</p>
                    <p class="price">{{item.price}}</p>
                </div>
            </div>
            <div class="ggt" v-for="item of micon">
                <img :src="item.img" alt="">
            </div>

            <!--家电-->
            <div class="Homeappliance">
                <div class="Homeappliance-tit">
                    <div class="Homeappliance-tit-left">
                        <span>家电</span>
                    </div>
                    <div class="Homeappliance-tit-right">
                        <span @mouseenter="msg=1" :class="Homeappliancetitrightcolor">家电</span>
                        <span @mouseenter="msg=2" :class="Homeappliancetitrightcolor">家电</span>
                        <span @mouseenter="msg=3" :class="Homeappliancetitrightcolor">家电</span>
                        <span @mouseenter="msg=4" :class="Homeappliancetitrightcolor">家电</span>
                    </div>
                </div>
                <div class="Homeappliance-con">
                    <div v-show="msg===1">1</div>
                    <div v-show="msg===2">2</div>
                    <div v-show="msg===3">3</div>
                    <div v-show="msg===4">4</div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                AlargerVersion: [],
                phone: [],
                micon: [],
                msg: 0,
            }
        },
        created() {
            this.axios.get("datalist.json").then((res) => {
                this.AlargerVersion = res.data.AlargerVersion;
            });
            this.axios.get("datalist.json").then((res) => {
                this.phone = res.data.phone;
            });
            this.axios.get("datalist.json").then((res) => {
                this.micon = res.data.micon;
                console.log(this.micon)
            })
        }
    }
</script>
<style>
    .Homeappliance {
        width: 1200px;
        border: 1px solid;
        margin-left: 108px;
        box-sizing: border-box;
    }

    .Homeappliance-tit-left {
        float: left;
    }

    .Homeappliance-tit-right {
        float: right;
    }

    .Homeappliance span {
        font-size: 30px;
    }

    .Homeappliance-con > div {
        width: 1200px;
        height: 700px;
        margin-top: 50px;
        border: 1px solid red;
        box-sizing: border-box;

    }
    .Homeappliancetitrightcolor{
        color: red;
    }














    .background {
        width: 1410px;
        background-color: #f5f5f5;
    }

    .background > span {
        display: block;
        height: 80px;
        line-height: 80px;
