        margin: 0 20px 20px 100px;
        font-size: 30px;
    }

    .phone {
        width: 1205px;
        height: 614px;
        margin-left: 108px !important;
    }

    .dutu {
        float: left;
        width: 240px !important;
        height: 614px !important;
        transition: 500ms;
    }

