        margin: 5px 4px 5px 4px;
        cursor: pointer;
    }

    .phone > div:hover {
        transform: scale(1.01, 1.01);
    }

    .phone > div:nth-child(6) {
        margin-right: 0;
    }

    .phone > div:nth-child(10) {
        margin-right: 0;
    }

    .xiaolist > img {
