        margin: 0 auto;
        text-align: center;
        color: #ffffff;
    }

    .xiaolist > p {
        text-align: center;
    }

    .xiaolist .name {
        font-size: 14px;
    }

    .xiaolist > .title {
        font-size: 12px;
        color: #b0b0b0;

    }

    .xiaolist > .price {
        color: #ff6700;
    }

    .ggt {
        width: 1200px;
        margin-left: 108px;
    }

    .HomeAppliance {
        width: 1200px;
        height: 614px;
        border: 1px solid;
