<template>
    <div>
        <div class="tabcon">
            <div class="tabwidth">
                <ul class="titlenav">
                    <li v-for="item of TabNav">
                        <router-link to="">{{item.name}}</router-link>
                    </li>
                </ul>
            </div>
            <div class="tabimg">
                <div v-for="item of NavImg">
                    <router-link to="">
                        <img :src="item.img" alt="">
                    </router-link>
                </div>
            </div>
        </div>
        <div v-for="item of ggt" class="ggt">
            <router-link to="">
                <img :src="item.img" alt="">
            </router-link>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                TabNav: [],
                NavImg: [],
                ggt: []
            }
        },
        created() {
            this.axios.get("datalist.json").then((res) => {
                this.TabNav = res.data.TabNav;
            });
            this.axios.get("datalist.json").then((res) => {
                this.NavImg = res.data.NavImg
            });
            this.axios.get("datalist.json").then((res) => {
                this.ggt = res.data.ggt
            })
        }
    }
</script>
<style>

    .tabcon{
        width: 1200px;
        margin-top: 20px;
        position: relative;
        margin-left: 108px;
    }
    .tabwidth{
        width: 200px;
        height: 170px;
        background-color: #5f5750;

    }
    .titlenav>li{
        border:  1.3px solid #757575;
        width: 60px;
        height: 53px;
        display: inline-block;
        text-align: center;
        padding-top: 30px;
        padding-left: 4px;
    }
    .tabcon li a{
        color: #a4a4a4;
        transition: 500ms;
        font-size: 12px;
        text-decoration: none;
    }
    .tabcon li:hover a{
        color: #ffffff;
    }
    .tabimg{
        height: 170px;
        width: 1000px;
        float: left;
        position: absolute;
        top: 0;
        left: 200px;
    }
    .tabimg>div{
        height: 170px;
        margin-left: 31px;
        float: left;
    }
    .tabimg>div>a>img{
        width: 302px;
        height: 170px;
    }
    .ggt{
        width: 1200px;
       margin: 20px 0 20px 108px;
    }
    .ggt img{
        height: 100%;
        width: 1200px;

    }

</style>
